body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Ubuntu", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#colorlib-hero .flexslider .slider-text > .slider-text-inner .btn {
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #000;
    padding: 10px 15px !important;
    border: none;
    font-weight: 600;
}
app {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.menuItem {
  font-size: 20px;
  font-weight: 600;
}

.services .icon:before {
  top: -30px;
  width: 0px;
  height: 0;
  border-style: solid;
  border-width: 0 60px 30px 60px;
  border-color: transparent transparent #2c98f0 transparent;
}

.services .icon:after {
  bottom: -30px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 30px 60px 0 60px;
  border-color: #2c98f0 transparent transparent transparent;
}

.partners {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.partnerLogo {
  padding: 4px;
  margin: 14px;
  min-width: 150px;
  max-width: 150px;
}

.partnerLogo img {
  width: 100%;
  height: auto;
}

@media screen and (min-width: 768px) {
  .partnerLogo {
    padding: 25px;
    margin: 25px;
    min-width: 250px;
    max-width: 250px;
  }
}

@media screen and (max-width: 768px) {
  .colorlib-experience, .colorlib-skills, .colorlib-education, .colorlib-blog, .colorlib-work, .colorlib-about, .colorlib-services, .colorlib-contact {
    padding-top: 1em;
    padding-bottom: 1em;
  }
}

.slideText {
  background-color: #dddddd59;
  padding: 20px;
  text-align: center;
}

h2 {
  margin: 0;
}

#colorlib-aside {
  height: 100%!important;;
  overflow: hidden;
}

#colorlib-heading {
  height: 100%!important;
}

.colorlib-heading {
font-size: 24px;
}

#colorlib-hero {
  margin-bottom: 8em;
}

#colorlib-hero .flexslider .slider-text > .slider-text-inner h1 {
  color: #423e3d;
}

.timeline-label {
  padding-bottom: 8px!important;
}
